import React , {Component} from 'react';
//import axios from "axios/index";
import './../../App.css';
import Weather from "../weather";
import Form from "../Form";
import Moment from 'react-moment';
import 'moment-timezone';
//import * as express from 'express';


//
class GetWeather extends Component {
    constructor() {
        super()        
        
        //const API_KEY = "3585775f387b0d0cba6c5b3dc41b8167"; 
        //const fullAddress = ''; 
                  

    this.state = {
      temperature: undefined,
      city: undefined,
      country: undefined,
      humidity: undefined,
      description: undefined,
      copyright: undefined,
      temperatureHigh: undefined,
      temperatureLow: undefined,
      windSpeed: undefined,
      visibility: undefined,
      precipProbability: undefined,
      countryFlag: undefined,
      error: undefined

    }; 
}

 countryFlag = '';
 check = false;
 newDate = new Date();
   //Start
   
   getWeather = async (e) => {
    e.preventDefault();
    let latitude = e.target.elements.latitude.value;
    let longitude = e.target.elements.longitude.value;
    let sdate = e.target.elements.date.value; 

    const address = e.target.elements.address.value;
    const city = e.target.elements.city.value;
    const state = e.target.elements.state.value; 

     this.fullAddress = address + ", " + city + ", " + state;
    
     

    if (latitude && longitude && sdate) {      
      this.fullAddress = 'Latitude: ' + latitude + ' and Longitude: ' +  longitude;
      this.countryFlag = "";
      this.check = true;
      // Check if the full address was passed in instead*
    } else{
      if (address && city && state && sdate) {
       this.countryFlag = "Y";
       this.check = true;
    //Get the Latitude and Longitude for the provided address
     //Google API_KEY= AIzaSyAPlfKsbpy6x4U6CKWdOWoxfSs2pgQ0DqA 
     // eslint-disable-next-line 
     const gAPIKey = "AIzaSyAPlfKsbpy6x4U6CKWdOWoxfSs2pgQ0DqA";
 //Test
 //https://maps.googleapis.com/maps/api/geocode/xml?address=1600+Amphitheatre+Parkway,+Mountain+View,+CA&key=AIzaSyAPlfKsbpy6x4U6CKWdOWoxfSs2pgQ0DqA
    const api_call_address = await fetch(`https://maps.googleapis.com/maps/api/geocode/json?address=${address},+${city},+${state}&key=${gAPIKey}`);
    const dataAddress = await api_call_address.json();
    const response = JSON.stringify(dataAddress);
    console.log("Data: " + response);

    //Check if the google maps returns a good response
    if(dataAddress.status === "REQUEST_DENIED"){
      //Google maps denied the request
      this.check = false;
      this.setState({
        error: "Error: " + dataAddress.status+ "=> " + dataAddress.error_message
        });
        alert("Error: " + dataAddress.status+ "=> " + dataAddress.error_message);

    } else{

    latitude = dataAddress.results[0].geometry.location.lat;
    longitude = dataAddress.results[0].geometry.location.lng;
    /*const newDate = new Date();
     const year = newDate.getFullYear();
    const month = newDate.getMonth();
    const day = newDate.getDay();
    const hour = newDate.getHours();
    const min = newDate.getMinutes();
    const sec = newDate.getSeconds(); 
    const dates = newDate.getUTCDate();
    const dates1 = newDate.getUTCMonth();
    const dates2 = newDate.getUTCDay();
    const time = newDate.getTime();*/   
 
    console.log("Latitude: " + latitude + "  Longitude: " + longitude + " sdate: " + sdate);
      }
    }
    }

    //If the check failed for some reason like no values provide or something else, don't call the Golang API
    if(this.check){
    sdate = sdate + "T12:00:00Z";
    let data;
    //Temp value for the time
    //sdate = '1584673998';
    //Since we are not passing sensesive information over the URL, we will use GET otherwise POST for security 
    //const api_call = await fetch(`https://arcules-test-101.appspot.com/api/weather/${latitude}&/${longitude}&/${sdate}`,
    //Dev
    //const api_call = await fetch(`http://localhost:3000/api/weather/${latitude}&/${longitude}&/${sdate}`);
    //Production
    const api_call = await fetch(`https://arcules-test-101.appspot.com/api/weather/${latitude}/${longitude}/${sdate}`);

    data = await api_call.json();
    data = data.message
    data = JSON.parse(data);
    //resolve(data ? JSON.parse(data) : {})
    const dataLength =  Object.keys(data).length;
    console.log("Data Length:" + dataLength)
    console.log("Data: " + JSON.stringify(data))
    
    
    if (latitude && longitude && sdate) {
        if(dataLength < 7){
            //Either you got a permission denied or the values you passed in are not valid
            this.setState({
            error: "Error: " + data.code + "=> " + data.error
            });
            alert(data.error);
        } else{
            //You got a good payload back
            //Safe the state of all variables in this component so we can use them later
            // description: (data.currently.summary ? data.daily.data[0].summary : ''),
         
        //const todayDate = newDate.getDate();
      this.setState({
        city: this.fullAddress,
        country: data.flags.units.toUpperCase(),

        temperature: data.currently.temperature,
        temperatureHigh: data.daily.data[0].temperatureHigh,
        temperatureLow: data.daily.data[0].temperatureLow,
        precipProbability: data.currently.precipProbability * 100,
        windSpeed: data.currently.windSpeed,
        visibility: data.currently.visibility,
        humidity: data.currently.humidity * 100,
        description: data.daily.data[0].summary,
        error: "",
        copyright: "Powered by Dark Sky",
        dateToFormat: this.newDate.getDate(),
        countryFlag: this.countryFlag            
      });
    }
    } else {
        //All input values are required
        //Safe the state of all variables in this component so we can use them later
       
      this.setState({
        temperature: undefined,
        city: undefined,
        country: undefined,
        humidity: undefined,
        description: undefined,
        copyright: undefined,
        temperatureHigh: undefined,
        temperatureLow: undefined,
        windSpeed: undefined,
        visibility: undefined,
        precipProbability: undefined,
        dateToFormat: this.newDate.getDate() ,
        countryFlag: undefined,
        error: "No proper values entered. Please makes sure you enter the right values including the DATE."
      });
      //Redunctant but it is ok for now
      alert("No proper values entered. Please makes sure you enter the right values including the DATE.");
    }
  } else{
    this.setState({
      temperature: undefined,
      city: undefined,
      country: undefined,
      humidity: undefined,
      description: undefined,
      copyright: undefined,
      temperatureHigh: undefined,
      temperatureLow: undefined,
      windSpeed: undefined,
      visibility: undefined,
      precipProbability: undefined,
      dateToFormat: this.newDate.getDate() ,
      countryFlag: undefined,
      error: "No proper values entered. Please makes sure you enter the right values including the DATE."
    });

  }

  }
   //Finish

    render() {
       
        return (

      <body>
        <div class="body_container">  
        <div className="twoColumn">        
          <p>This is a training exercise written with a Golang back-end which consumes a Restfull API from <a href="https://darksky.net/forecast/40.7127,-74.0059/us12/en" target="_blank" rel="noopener noreferrer">Dark Sky</a> weather service and passes the payload to the React fron-end to complete the UI</p>
          <p>This app ultimately would be enhanced to accept the ZIP-CODE instead of the coordinates during the next update.</p>
          <p>For the app to work, the user would need to provide a valid latitude and longitude plus the date in order to get the weather forecast information</p>
           
          </div>
          {/* <div className="form-group">
                        Please enter your ZIP code: <input type="number" className="form-control" value={this.state.zip} onChange={(event) => this.setState({zip: event.target.value})}/>
                    </div>
                    <button className="btn btn-success btn-block" onClick={this.postDataHandler}><i className="fa fa-plus"></i> Get weather</button>
        </div> */}
        
                {/* Another Div */}
                <div className="form-container">
                  <Form getWeather={this.getWeather}
                  today={<Moment date={this.dateToFormat} />}
                  />
                  <Weather 
                  city={this.state.city}
                  country={this.state.country}

                    temperature={this.state.temperature} 
                    humidity={this.state.humidity}
                    
                    description={this.state.description}
                    error={this.state.error}
                    copyright={this.state.copyright}

                    temperatureHigh={this.state.temperatureHigh}
                    temperatureLow={this.state.temperatureLow}
                    windSpeed={this.state.windSpeed}
                    visibility={this.state.visibility}
                    precipProbability={this.state.precipProbability}
                    countryFlag={this.countryFlag}
                    
                     />
                </div>
                </div>
      </body>
    
            )
    }
}

export default GetWeather;