import React from 'react';

/* const weather = (props) => (
    <tr>
        <th scope="row">time</th><th scope="row">summary</th><th scope="row">icon</th><th scope="row">earestStormDistance</th><th scope="row">nearestStormBearing</th><th scope="row">precipIntensity</th>
        <td>{props.time}</td><td>{props.summary}</td><td>{props.icon}</td><td>{props.nearestStormDistance}</td><td>{props.nearestStormBearing}</td><td>{props.precipIntensity}</td>
        
    </tr> 
    ); */
    const Weather = props => (
        <div className="weather__info">
         {	
             props.city && <sapn className="weather__city_info"> Your forecast for: 
                 <span className="weather__value"> { props.city }</span>                 
             </sapn>
            
         }
          {	
            props.country && props.countryFlag &&
                 <span className="weather__value"> , { props.country }</span>
                
             
            
         }
         
         <br></br> <hr></hr>
         <div className="twoColumn">
         { 	
         
             props.temperature && <p className="weather__key"> Temperature: 
                 <span className="weather__value"> { props.temperature }<span>&#x2109;</span>	</span>
             </p> 
         }
         { 	
             props.temperatureHigh && <p className="weather__key"> High Temperature: 
                 <span className="weather__value"> { props.temperatureHigh }<span>&#x2109;</span>	</span>
             </p> 
         }
         { 	
             props.temperatureLow && <p className="weather__key"> Low Temperature: 
                 <span className="weather__value"> { props.temperatureLow }<span>&#x2109;</span>	</span>
             </p> 
         }
         { 	
             props.humidity && <p className="weather__key"> Humidity: 
                 <span className="weather__value"> { props.humidity }<span>&#x25;</span></span>
             </p> 
         }
        { 	
             props.precipProbability && <p className="weather__key"> Chance of rain: 
                 <span className="weather__value"> { props.precipProbability }<span>&#x25;</span></span>
             </p> 
         }

           { 	
             props.windSpeed && <p className="weather__key"> WindSpeed: 
                 <span className="weather__value"> { props.windSpeed } mph</span>
             </p> 
         }
           { 	
             props.visibility && <p className="weather__key"> Visibility: 
                 <span className="weather__value"> { props.visibility }+ mi&larr;</span>
             </p> 
         }
         { 	
             props.description && <p className="weather__key"> Conditions: 
                 <span className="weather__value"> { props.description } </span>
         </p> 
         }
         </div>
         { 
             props.error && <p className="errorMsg white">{ props.error }</p>  
         }
         { 
             props.copyright && <p className="weather__copyright">
                 <br></br> <hr></hr><a href="https://darksky.net/poweredby/" target="_blank" rel="noopener noreferrer">
                 { props.copyright }
                 </a>
                 </p>  
         }


        </div>
    );
    
    export default Weather;